import { domUtils } from '@ali/4ever-cangjie';

export function findDOMNodeByVoidKey(
  contentId: string,
  container: HTMLElement,
) {
  const cmtNode = container.querySelector(`[data-comment-void="${contentId}"]`);
  const key = cmtNode?.getAttribute('data-comment-nodekey');
  const domNode = key && domUtils.findDOMNodeSafely(key, container);
  return domNode || null;
}
